import React, { useState } from "react";
import DatePicker from "react-datepicker";

import "react-datepicker/dist/react-datepicker.css";

const Timkiemtour = () =>{
    const [startDate, setStartDate] = useState(new Date());
        return (
            <div className='showtimkiem'>
                <h3>TM KIM TOUR</h3>
                <div className='luachontimkiemtour'>
                    <div className='optiontimkiem'>
                        <p>Ni khi hnh</p>
                        <select value="Ni khi hnh">
                            <option value="1">Ni khi hnh</option>
                            <option value="2"> Lt</option>
                            <option value="3"> Nng</option>
                            <option value="4">Hi An</option>
                            <option value="5">H Ch Minh</option>
                            <option value="6">Nha Trang</option>
                            <option value="7">Phan Thit</option>
                        </select>
                    </div>
                    <div className='optiontimkiem'>
                        <p>Loi Tour</p>
                        <select value="Chn loi Tour">
                            <option value="1">Chn loi Tour</option>
                            <option value="2">Tour trong nc</option>
                            <option value="3">Tour nc ngoi</option>
                        </select>
                    </div>
                    <div className='optiontimkiem'>
                        <p>Ni n</p>
                        <select value="Chn ni n">
                            <option value="1">Chn ni n</option>
                            <option value="2">Hoa K</option>
                            <option value="3">Canada</option>
                        </select>
                    </div>
                    <div className='optiontimkiem'>
                        <p>Ngy khi hnh</p>
                        <DatePicker selected={startDate} onChange={(date) => setStartDate(date)} />
                    </div>
                    <div className='optiontimkiem'>
                        <p>Gi Tour</p>
                        <select value="Chn mc gi">
                            <option value="1">Chn mc gi</option>
                            <option value="2">Di 1 triu</option>
                            <option value="3">T 3 - 5 triu</option>
                        </select>
                    </div>
                    <div className='optiontimkiem'>
                    <p>.</p>
                        <button className="buttontimkiemtour">
                            Tm kim
                        </button>
                    </div>
                </div>
            </div>
        )
}

export default Timkiemtour
